<template>
    <div class="card" style="width: 18rem;" v-onHover @click="goTodetail(product.productid)">
            <img :src="product.image" class="card-img-top" alt="...">
            <div class="card-body">
                <h5 class="card-title">{{product.name}}</h5>
                <h6 class="card-title">&#x20b9;{{product.price}}</h6>
                <p class="card-text">{{product.description}}</p>
            </div>
    </div>
</template>
<script>
export default {
    name: 'Product',
    props: ['product'],
    directives: {
        onHover: {
            // directive definition
            inserted: function (el) {
                el.onmouseover = function() {
                    el.style.border = "2px solid #ccc";
                },
                el.onmouseleave = function() {
                    el.style.border = "none";
                }
            }
        }
    },
    methods: {
        goTodetail(prodId) {
            console.log(prodId);
            this.$router.push({name:'product',params:{id:prodId}})
        }
    }
}
</script>
<style scoped>
.card {
    cursor: pointer;
}
</style>

