<template>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
        <a class="navbar-brand" href="#">Navbar</a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto">
                <li class="nav-item">
                    <router-link exact class="nav-link" to="/">Home</router-link>
                </li>
                <li class="nav-item" v-if="!isUserLoggedIn">
                    <router-link class="nav-link" to="/login">Login</router-link>
                </li>
                <li v-if="!isUserLoggedIn">
                    <router-link class="nav-link" to="/sign-up">Sign Up</router-link>
                </li>
            </ul>
            <div v-if="isUserLoggedIn">
                <h1 style="color:#fff">Welcome!!</h1>
                <a href="javascript:void(0)" @click="logout">Logout</a>&nbsp;&nbsp;&nbsp;&nbsp;
                <a href="javascript:void(0)" @click="goToCart">Cart</a>
            </div>
        </div>
    </nav>
</template>
<script>
export default {
    name: 'navbar',
    data: function() {
        return {
            isUserLoggedIn: false
        }
    },
    created: function() {
        console.log("states", this.$store.state);
        this.isUserLoggedIn = this.$store.state.isLoggedIn;
        this.$store.subscribe(() => {
            console.log("store is getting updated", this.$store.state);
            this.isUserLoggedIn = this.$store.state.isLoggedIn;
        })
    },
    methods: {
        logout: function() {
            this.$store.commit("LOGOUT");
        },
        goToCart: function() {
            this.$router.push('/cart');
        }
    }
}
</script>

