<template>
    <div v-if="product" class="container">
        <div class="row">
           <div class="col-md-6">
               <img :src="product.image">
           </div>
           <div class="spec col-md-6">
               <label class="form-control">{{product.name}}</label>
               <label class="form-control">{{product.price}}</label>
               <label class="form-control">{{product.brand}}</label>
               <label class="form-control">{{product.quantity}}</label>
               <label class="form-control">{{product.seller}}</label>
               <label class="form-control">{{product.rating}}</label>
               <p>{{product.description}}</p>          
               <button class="btn btn-info" @click="addToCart">Add To cart</button>
               <button class="btn btn-warning" >Buy Now</button>  
           </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
const url = "https://learningmeanwithashu.herokuapp.com/api/allproducts";
export default {
    name: 'productDetail',
    data: function() {
        return {
            product: {
                image:'data:image/jpeg;base64,',
                name:"oneplus6",
                brand:"one plus series",
                quantity:42,
                seller:"Amazon",
                rating:4.8,
                description:"phone in this range with best features",
                price:35000
            }
        }
    },
    methods: {
        addToCart: function() {
            if (localStorage.getItem('token')) {
                this.$store.commit('ADDTOCART', this.product);
                this.$router.push('/cart');
            } else {
                this.$router.push('/login');
            }
        }
    },
    created() {
        axios(url, {
            method: 'get'
        })
        .then(response => {
            // handle success
            console.log(response.data.data);
            this.products = response.data.data;
        });
    }
}
</script>
<style scoped>
.container {
    margin-top: 20px;
}
</style>
