<template>
    <form class="form-inline my-2 my-lg-0">
        <input class="form-control mr-sm-2" type="text" v-model="email" placeholder="Email">
        <input class="form-control mr-sm-2" type="password" v-model="password" placeholder="Password">
        <input class="form-control mr-sm-2" type="checkbox" v-model="rememberMe"> Keep Me Logged In
        <button class="btn btn-outline-success my-2 my-sm-0" type="button" @click="login">Login</button>
    </form>
</template>
<script>
import axios from 'axios';
const loginUrl = "https://learningmeanwithashu.herokuapp.com/api/login";
export default {
    name: 'Login',
    data: function() {
        return {
            email: null,
            password: null,
            rememberMe: null
        }
    },
    methods: {
        login: function() {
            var payload = {
                email: this.email,
                password: this.password
            };
            axios.post(loginUrl, payload)
              .then((response) => {
                this.userLoggedIn = true;
                if(this.rememberMe) {
                    console.log(response);
                    console.log("Before Mutation", this.$store.state);
                    localStorage.setItem('token', response.data.token);
                    this.$store.commit("LOGIN", true);
                    console.log("after Mutation", this.$store.state);
                }
                this.$router.push('/') 
              })
              .catch(function (error) {
                console.log(error);
              });
        }
    }
}
</script>
