<template>
    <div class="products">
        <div v-for="product of products" class="product" v-if="cartLength">
            <img :src="product.image" >
            <h1>{{product.name}}</h1>
            <h1>{{product.price}}</h1>
            <a href="javascript:void(0)" @click="removeItem(product)">Remove</a>
        </div>
        <div v-if="!cartLength">
            Your cart is empty!!
        </div>
    </div>
</template>
<script>
export default {
    data: function() {
        return {
            products: []
        }
    },
    created: function() {
        this.products = this.$store.state.cartItems;
    },
    methods: {
        removeItem: function(product) {
            this.$store.commit("REMOVEITEM", product);
        }
    },
    computed: {
        cartLength: function() {
            return this.products.length;
        }
    }
}
</script>
<style scoped>
.products {
    text-align: left;
}
.product {
   border:3px solid #ccc;
    padding: 20px;
    margin:10px;
}
</style>

