<template>
    <div class="container">
        <div class="row">
            <div class="col-md-4" :key="key" v-for="(prod, key) of products">
                <product :product="prod"></product>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import Product from './product';
const url = "https://learningmeanwithashu.herokuapp.com/api/allproducts";
export default {
    name: 'Home',
    data: function() {
        return {
            products: []
        }
    },
    created: function() {
        axios(url, {
            method: 'get'
        })
        .then(response => {
            // handle success
            console.log(response.data.data);
            this.products = response.data.data;
        });
    },
    components: {Product}
    
}
</script>
